import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What is Java Full Stack Development?",
      answer: "Java Full Stack Development refers to the development of both the front end (client side) and back end (server side) portions of web applications using Java technologies. It involves using frameworks like Spring Boot for backend development and frameworks like React or Angular for front end development.",
    },
    {
      question: "What are the essential skills for a Java Full Stack Developer?",
      answer: "Essential skills for a Java Full Stack Developer include proficiency in Java programming, knowledge of Spring framework, understanding of RESTful APIs, familiarity with front end technologies like HTML, CSS, and JavaScript, and experience with databases like MySQL or PostgreSQL.",
    },
    {
      question: "Why is Java widely used in Full Stack Development?",
      answer: "Java is widely used in Full Stack Development due to its platform independence, strong community support, robustness, scalability, and security features. It allows developers to build large-scale, enterprise-level applications efficiently.",
    },
    {
      question: "What are the advantages of using Spring Boot in Java Full Stack Development?",
      answer: "Spring Boot simplifies the development of production-ready applications by providing default configurations and eliminating the need for complex XML configurations. It promotes convention over configuration and allows developers to focus more on business logic rather than boilerplate code.",
    },
    {
      question: "How does Hibernate benefit Java Full Stack Development?",
      answer: "Hibernate is an object-relational mapping (ORM) framework for Java that simplifies database access and management in Java applications. It allows developers to map Java objects to database tables and perform CRUD operations without writing native SQL queries, thus improving productivity and maintainability.",
    },
    {
      question: "What is the role of RESTful APIs in Java Full Stack Development?",
      answer: "RESTful APIs (Representational State Transfer) are essential in Java Full Stack Development for enabling communication between client-side applications (front end) and server-side applications (back end). They use standard HTTP methods like GET, POST, PUT, DELETE to perform CRUD operations on resources.",
    },
    {
      question: "How can microservices architecture be implemented in Java Full Stack Development?",
      answer: "Microservices architecture in Java Full Stack Development involves breaking down large monolithic applications into smaller, independent services that can be deployed, managed, and scaled individually. Java frameworks like Spring Cloud provide tools and libraries to implement microservices patterns such as service discovery, load balancing, and circuit breakers.",
    },
    {
      question: "What are the common testing frameworks used in Java Full Stack Development?",
      answer: "Common testing frameworks used in Java Full Stack Development include JUnit for unit testing, Mockito for mocking dependencies, Selenium for automated browser testing, and Postman for API testing. These frameworks help ensure the quality and reliability of Java applications throughout the development lifecycle.",
    },
    {
      question: "How does Docker support Java Full Stack Development?",
      answer: "Docker is a containerization platform that allows developers to package Java applications and their dependencies into lightweight, portable containers. It simplifies application deployment, improves scalability, and ensures consistency across different environments, making it ideal for Java Full Stack Development.",
    },
    {
      question: "What are the career prospects for Java Full Stack Developers?",
      answer: "Java Full Stack Developers are in high demand across various industries due to their expertise in both front end and back end technologies. They can pursue roles such as Full Stack Developer, Java Developer, Software Engineer, Technical Lead, and Solution Architect, with opportunities for career growth and specialization.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions about Java Full Stack Development
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
